body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.colors-main {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 98vh;
  width: 15vw;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  background-color: black;
  padding: 0 10px 0 10px;
  border-right: 1px solid white;
  color: white; }

.directions {
  background-color: purple;
  border-radius: 10px;
  margin: 10px 5px 10px 5px;
  text-align: center; }

.colors-box {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  margin: 10px 0 10px 0;
  padding: 5px;
  height: 50vh; }

.peg,
.colors-main button {
  width: 7vh;
  height: 7vh;
  margin: 5%;
  border-radius: 50%;
  text-align: center;
  vertical-align: middle;
  line-height: 8vh; }

.peg:hover {
  cursor: pointer; }

.w-peg {
  background-image: radial-gradient(#ebebeb, white 55%, #333333); }

.r-peg {
  background-image: radial-gradient(#d60000, red 55%, black); }

.o-peg {
  background-image: radial-gradient(#d68b00, orange 55%, black); }

.y-peg {
  background-image: radial-gradient(#d6d600, yellow 55%, black); }

.gn-peg {
  background-image: radial-gradient(#005700, green 55%, black); }

.b-peg {
  background-image: radial-gradient(#0000d6, blue 55%, black); }

.p-peg {
  background-image: radial-gradient(#570057, purple 55%, black); }

.pi-peg {
  background-image: radial-gradient(#ff40a0, hotpink 55%, black); }

.g-peg {
  background-image: radial-gradient(#6c6c6c, gray 55%, black); }

.main {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  background-color: turquoise; }

.peg-board {
  display: -webkit-flex;
  display: flex;
  background-color: black; }

.grid {
  display: grid;
  grid-template-columns: repeat(30, 3.33%);
  width: 98vh;
  height: 98vh;
  margin: 0 10px 0 10px; }

.hole-border {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  background-color: black; }

.hole-border div {
  border-radius: 50%;
  width: 80%;
  height: 80%;
  margin: 0; }

#g {
  background-color: #161616; }

#w {
  background-image: radial-gradient(lightgray 45%, #545454); }

#r {
  background-image: radial-gradient(red 45%, black); }

#o {
  background-image: radial-gradient(orange 45%, black); }

#y {
  background-image: radial-gradient(yellow 45%, black); }

#gn {
  background-image: radial-gradient(green 45%, black); }

#b {
  background-image: radial-gradient(blue 45%, black); }

#p {
  background-image: radial-gradient(purple 45%, black); }

#pi {
  background-image: radial-gradient(hotpink 45%, #690035); }

#w.on {
  background-color: white;
  box-shadow: 0px 0px 20px white;
  background-image: none; }

#r.on {
  background-color: #ff3333;
  box-shadow: 0px 0px 20px #ff6666;
  background-image: none; }

#o.on {
  background-color: #ffb733;
  box-shadow: 0px 0px 20px #ffc966;
  background-image: none; }

#y.on {
  background-color: #ffff33;
  box-shadow: 0px 0px 20px #ffff66;
  background-image: none; }

#gn.on {
  background-color: #00b300;
  box-shadow: 0px 0px 20px #00e600;
  background-image: none; }

#b.on {
  background-color: #3333ff;
  box-shadow: 0px 0px 20px #6666ff;
  background-image: none; }

#p.on {
  background-color: #b300b3;
  box-shadow: 0px 0px 20px #e600e6;
  background-image: none; }

#pi.on {
  background-color: #f6489f;
  box-shadow: 0px 0px 20px #f879b9;
  background-image: none; }

.btn-box {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
  background-color: black;
  border-left: 1px solid white;
  padding: 0 10px 0 10px;
  height: 98vh;
  color: white; }

.btn-box div {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center; }

.btn-box button {
  cursor: pointer; }

button:focus {
  outline: 0; }

.reset {
  border-radius: 50%;
  background-color: black;
  border: 1px solid white;
  color: red;
  font-size: 1.8vh;
  width: 9vh;
  height: 9vh;
  margin: 2px; }

.reset:hover {
  background-color: white;
  border: 1px solid black; }

.light-switch {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  width: 7vh;
  height: 11vh;
  margin: 2px;
  padding: 0;
  border-radius: 5px; }
  .light-switch .flipper {
    background-color: purple;
    width: 6.5vh;
    height: 2.5vh;
    margin: 0.5vh 0 0.5vh 0;
    border-radius: 4px; }

.lightOn {
  border: none;
  background-image: linear-gradient(orange 30%, #ffdb99); }

.lightOff {
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  border: none;
  background-color: #b37400; }

